import React, { lazy, Suspense, useEffect, useState } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import LoaderSpinner from "react-loader-spinner";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

// import Projects from './Pages/Projects'
import Layout from "./Pages/Layout";

import HomeLoader from "./Pages/Home/Loader";
// import AboutMe from "./Pages/AboutMe";
// import Projects from './Pages/Projects'
// import Training from "./Pages/Training";
// import Techs from "./Pages/Techs";
// import Error404 from "./Pages/Error404";
// import TechDetail from "./Pages/Techs/TechDetail";
// import CertsPlatzi from "./Pages/Training/CertsPlatzi";
// import CertDetail from "./Pages/Training/CertDetail/";

const Home = lazy(() => import("./Pages/Home"));
const AboutMe = lazy(() => import("./Pages/AboutMe"));
const Training = lazy(() => import("./Pages/Training"));
const Techs = lazy(() => import("./Pages/Techs"));
const Error404 = lazy(() => import("./Pages/Error404"));
const TechDetail = lazy(() => import("./Pages/Techs/TechDetail"));
const CertsPlatzi = lazy(() => import("./Pages/Training/CertsPlatzi"));
const CertDetail = lazy(() => import("./Pages/Training/CertDetail/"));

const App = () => {
  const [location, setLocation] = useState("");
  const [loader, setLoader] = useState(null);
  const getPathname = (pathname) => {
    setLocation(pathname);
  };

  useEffect(() => {
    switch (location) {
      case "/":
        setLoader(<HomeLoader className="home-loader" />);
        break;
      default:
        setLoader(
          <LoaderSpinner
            type="Puff"
            color="white"
            style={{ marginTop: "2em", textAlign: "center" }}
          />
        );
        break;
    }
  }, [location]);

  return (
    <div>
      <Router>
        <Layout getPathname={getPathname}>
          <Suspense fallback={loader}>
            <Switch>
              <Route path="/" exact component={Home} />
              <Route path="/aboutme" exact component={AboutMe} />
              {/* <Route path="/projects" exact component={Projects} /> */}
              <Route path="/technologies" exact component={Techs} />
              <Route path="/technologies/:tech" exact component={TechDetail} />
              <Route path="/training" exact component={Training} />
              <Route path="/training/platzi" exact component={CertsPlatzi} />
              <Route path="/training/:cert" exact component={CertDetail} />
              <Route path="*" component={Error404} />
            </Switch>
          </Suspense>
        </Layout>
      </Router>
    </div>
  );
};

export default App;
